import React from 'react';
import styled from 'styled-components';
import Col from 'react-bootstrap/Col';


const Styles = styled.div`
    height: 310px;
    width: 320px;
    margin: 0 auto;
    margin-top: 35px;
`;

const CustomImage = styled.div`
    background: url(${props => props.image}) no-repeat;
    height: 190px;
    width: 320px;
`;

const CustomTitle = styled.div`
    padding: 25px;
    padding-left: 20px;
    height: 125px;
    background-color: white;
`;


export const CustomCard = (props) => {
    return (
        <Col xl={4} lg={6} md={6} className = 'justify-content-start'>
            <Styles>
                <CustomImage image = {props.image}></CustomImage>
                <CustomTitle>
                    <h6 style = {{fontSize: '13px'}}>{props.subTitle}</h6>
                    <a style = {{fontSize: '15px', fontWeight: 900, color: 'black', textShadow: '0px 0.1px, 0.1px 0px, 0.1px 0.1px'}} href = {props.link}>{props.title}</a>
                </CustomTitle>
            </Styles>
        </Col>
    );
}
